import React from "react";
import TodoInput from "./components/TodoInput";
import TodoItems from "./components/TodoItems";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      todos: [],
      input: "",
      submited: false,
      editTime: false,
      idEdit: null
    };

    this.handleChange = this.handleChange.bind(this);
    this.deleteTodo = this.deleteTodo.bind(this);
    this.addTodo = this.addTodo.bind(this);
    this.deleteAll = this.deleteAll.bind(this);
    this.editTodo = this.editTodo.bind(this);
    this.getIdEdit = this.getIdEdit.bind(this);
  }

  handleChange(e) {
    this.setState({
      input: e.target.value,
      submited: false
    });
  }

  deleteTodo(id) {
    const todosFiltered = this.state.todos.filter(todo => {
      return todo.id !== id;
    });
    this.setState({
      todos: todosFiltered,
      editTime: false,
      input: ""
    });
  }

  deleteAll() {
    this.setState({
      todos: []
    });
  }

  addTodo(e) {
    e.preventDefault();
    const updTodos = [
      ...this.state.todos,
      { id: Math.random(), content: this.state.input }
    ];
    this.setState({
      todos: updTodos,
      submited: true,
      input: ""
    });
  }

  getIdEdit(id) {
    const editedTodo = this.state.todos.filter(todo => {
      return todo.id === id;
    });

    this.setState({
      editTime: true,
      idEdit: id,
      input: editedTodo[0].content
    });
  }

  editTodo(e) {
    e.preventDefault();

    const updTodos = this.state.todos.map(todo => {
      return todo.id === this.state.idEdit
        ? { id: this.state.idEdit, content: this.state.input }
        : todo;
    });

    this.setState({
      todos: updTodos,
      editTime: false,
      submited: true,
      input: ""
    });
  }

  render() {
    return (
      <div className="container">
        <h1>todo input</h1>
        <TodoInput
          handleChange={this.handleChange}
          addTodo={this.addTodo}
          editTodo={this.editTodo}
          submited={this.state.submited}
          editTime={this.state.editTime}
          input={this.state.input}
        />
        <div className="todolist-container">
          <h2>Todo List</h2>
          <ul>
            <TodoItems
              todos={this.state.todos}
              deleteTodo={this.deleteTodo}
              getIdEdit={this.getIdEdit}
            />
          </ul>
        </div>
        <button className="clear" onClick={this.deleteAll}>
          Clear List
        </button>
      </div>
    );
  }
}

export default App;
